<template>
  <div class="stock-search-wrapper">
    <el-input
      v-model="stockSearchedInfo.stock_id"
      placeholder="Please Input Stock Code"
      @keyup.enter="search">
      <template #append>
        <el-button
          icon="el-icon-search"
          type="primary"
          @click="search">
        </el-button>
      </template>
    </el-input>
  </div>
  <item-tab
    class="stock-focus"
    title="Stock Focus">
    <div class="stock-focus__content">
      <statistic-card
        icon="el-icon-s-marketing"
        v-for="(item,index) in stockFocus"
        :key="item.stock_id"
        :index="index"
        :title="item.stock_publisher"
        :value="item.stock_id"
        @click="toStockInfo">
      </statistic-card>
    </div>
  </item-tab>
</template>

<script>
import { defineComponent, onMounted, ref } from 'vue'
import { ElSelect, ElOption, ElInput, ElButton } from 'element-plus'
import { useRouter } from 'vue-router'
import { ROUTE_CONSOLE_STOCKINFO } from '@/router/config'
import { getPublisherByStockId, isNumber, typeToString } from '@/utils/func'
import itemTab from '@/components/item-tab/index'
import statisticCard from '@/components/statisticsCard/src/card'
import GlobalData from '@/store/index'
import axios from 'axios'
import { stock_api } from '@/store/api'
export default defineComponent({

  components: {
    ElSelect,
    ElOption,
    ElInput,
    ElButton,
    itemTab,
    statisticCard,
  },

  setup(props, { attrs, slots, emit }) {
    // 股票信息
    const stockSearchedInfo = ref({
      stock_id: '',
    })
    // 搜索目标股票
    const router = useRouter()
    const search = () => {
      GlobalData.searchedStockInfo = stockSearchedInfo.value
      router.push(ROUTE_CONSOLE_STOCKINFO.use())
    }

    // 前往股票详情页
    const toStockInfo = (index) => {
      const notNumber = !isNumber(index)
      if(notNumber) return
      GlobalData.searchedStockInfo = GlobalData.stockFocus.value[index]
      router.push(ROUTE_CONSOLE_STOCKINFO.use())
    }

    const getFocusStock = () => {
      axios
        .get(stock_api)
        .then(res => {
          // 根据股票代码求出股票所在证券商
          const data = res.data.data || []
          const length = data.length
          GlobalData.stockFocus.value = data
          for(let i=0;i<length;++i) {
            const stock = GlobalData.stockFocus.value[i]
            stock.stock_publisher = getPublisherByStockId(stock.stock_id)
          }
        })
    }

    onMounted(() => {
      getFocusStock()
    })

    return {
      stockSearchedInfo,
      search,
      stockFocus: GlobalData.stockFocus,
      toStockInfo,
    }
  }
})
</script>

<style lang="scss">
  .stock-search-wrapper {
    display: flex;
    align-items: center;
    width: 80%;
    min-width: 500px;
    height: 70px;
    background: #fff;
    border-radius: 12px;
    overflow: hidden;
    margin: 0 auto;
    box-shadow: 0 10px 30px 0 #0000001a;

    div,input {
      height: 100%;
      border: none !important;
      background-color: #fff;
    }

    .el-select {
      width: 150px;
      height: 100%;
      border-right: 1px solid #ebeef5 !important;
      div,input {
        width: 100%;
        height: 100%;
        border: none !important;
      }
    }

    .el-button--primary {
      color: var(--color-primary) !important;
    }

  }

  .stock-focus {
    width: 90%;
    margin: 50px auto !important;
    box-shadow: 0 10px 30px 0 #0000001a !important;
    overflow: hidden;
    &__content {
      display: flex;
      flex-wrap: wrap;
      padding: 20px 10px;

      > * {
        margin-right: 10px;
      }

      .statistics-card:hover {
        background-color: #e7e7e7;
        cursor: pointer;
        transition: background-color .1s;
      }
    }
  }
</style>
