<template>
  <item-tab
    :needTitle="false"
    class="stock-id">
    <div>
      <i
        class="el-icon-back"
        @click="back">
      </i>
      <span>
        {{ searchedStockInfo.stock_id }}
      </span>
    </div>
    <el-button
      v-if="!isFocus"
      type="primary"
      @click="focusStock">
      Focus
    </el-button>
  </item-tab>
  <div class="stock-predictor">
    <item-tab title="Stock Price" style="width:70%">
      <high-chart
        chartType="stock"
        :options="stockPriceOptions">
      </high-chart>
    </item-tab>
    <item-tab
      title="Predict Rate of Buy, Sell or Hold"
      style="width:30%">
      <high-chart
        :options="stockPredictorOptions">
      </high-chart>
    </item-tab>
  </div>
  <item-tab title="Candle Line">
    <high-chart
      chartType="stock"
      :options="KLineOptions">
    </high-chart>
  </item-tab>
  <div class="stock-score">
    <item-tab
      title="Stock Score"
      style="width:48%">
      <high-chart
        chartType="more"
        :options="stockScoreOptions">
      </high-chart>
    </item-tab>
    <item-tab
      title="Stock Optimism"
      style="width:48%">
      <high-chart
        chartType="solidgauge"
        :options="stockEmotionOptions">
      </high-chart>
    </item-tab>
  </div>

</template>

<script>
import { computed, defineComponent, onMounted, reactive, toRef } from 'vue'
import { useRouter } from 'vue-router'
import { ElButton } from 'element-plus'
import { emotion_analysis_api, stock_api, stock_price_api, stock_score_api } from "@/store/api";
import highChart from "@/components/highCharts/index";
import itemTab from "@/components/item-tab/index";
import axios from 'axios';
import GlobalData from '@/store/index'
import { getPublisherByStockId } from '@/utils/func';
export default defineComponent({
  components: {
    itemTab,
    highChart,
    ElButton,
  },
  setup(props, { attrs, slots, emit }) {
    const dataGroup = [
      [
        'week',
        [1]
      ],
      [
        'month',
        [1, 2, 3, 4, 6]
      ]
    ]
    // 股票价格图表选项
    const stockPriceOptions = reactive({
        rangeSelector: {
          selected: 2
        },
        plotOptions: {
          series: {
            showInLegend: true
          }
        },
        tooltip: {
          split: false,
          shared: true
        },
        series: [{
          id: '000001',
          name: 'Price',
          data: [],
          zoneAxis: 'x',
          zones: [{
            value: Date.UTC(2021, 2, 4),
            color: 'rgb(124, 181, 236)'
          },{
            color: 'rgb(241, 92, 128)'
          }]
        }]
    })
    // 股票打分图标选择
    const stockScoreOptions = reactive({
      chart: {
				polar: true,
        type: 'area',
      },
      title: {
        text: 'score(0-10)'
      },
      pane: {
        size: '80%'
      },
      xAxis: {
        tickmarkPlacement: 'on',
			  lineWidth: 0,
        categories: ['盈利能力', '每股收益增长率', '市盈率(总市值/净利润)', '财务状况', '市净率', '市盈率(TTM)'],
      },
      yAxis: {
        gridLineInterpolation: 'polygon',
				min: 0,
        max: 10,
      },

      series: [
        {
          name: 'score',
          data: [],
          pointPlacement: 'on'
        },
      ]
    })
    // 股票情感图标选项
    const stockEmotionOptions = reactive({
      chart: {
        type: 'solidgauge'
      },
      title: null,
      pane: {
        center: ['50%', '85%'],
        startAngle: -90,
        endAngle: 90,
        background: {
          innerRadius: '60%',
          outerRadius: '100%',
          shape: 'arc'
        }
      },
      yAxis: {
        stops: [
          [0.1, '#DF5353'], // green
          [0.6, '#DDDF0D'], // yellow
          [0.9, '#55BF3B'] // red
        ],
        lineWidth: 0,
        minorTickInterval: null,
        tickPixelInterval: 400,
        tickWidth: 0,
        title: {
          y: -70
        },
        labels: {
          y: 16
        }
      },
      plotOptions: {
        solidgauge: {
          dataLabels: {
            y: 5,
            borderWidth: 0,
            useHTML: true
          }
        }
      },
      series: [{
        name: '乐观程度',
        data: [],
      }]
    })
    // 股票预测仪表盘
    const stockPredictorOptions = reactive({
      chart: {
				plotBackgroundColor: null,
				plotBorderWidth: null,
				plotShadow: false,
				type: 'pie'
      },
      title: {
        text: ''
      },
      tooltip: {
        pointFormat: '{series.name}: <b>{point.percentage:.1f}%</b>'
      },
      plotOptions: {
        pie: {
          allowPointSelect: true,
          cursor: 'pointer',
          dataLabels: {
            enabled: true,
            format: '<b>{point.name}</b>: {point.percentage:.1f} %',
          }
        }
      },
      series: [{
        name: 'Value',
				colorByPoint: true,
				data: []
      }]
    })
    // 股票蜡烛图
    const KLineOptions = reactive({
      rangeSelector: {
        selected: 1,
        inputDateFormat: '%Y-%m-%d'
      },
      xAxis: {
        dateTimeLabelFormats: {
          millisecond: '%H:%M:%S.%L',
          second: '%H:%M:%S',
          minute: '%H:%M',
          hour: '%H:%M',
          day: '%m-%d',
          week: '%m-%d',
          month: '%y-%m',
          year: '%Y'
        }
      },
      tooltip: {
        split: false,
        shared: true,
      },
      yAxis: [{
        labels: {
          align: 'right',
          x: -3
        },
        title: {
          text: '股价'
        },
        height: '65%',
        resize: {
          enabled: true
        },
        lineWidth: 2
      }, {
        labels: {
          align: 'right',
          x: -3
        },
        title: {
          text: '成交量'
        },
        top: '65%',
        height: '35%',
        offset: 0,
        lineWidth: 2
      }],
      series: [{
        type: 'candlestick',
        name: 'Price',
        color: 'green',
        lineColor: 'green',
        upColor: 'red',
        upLineColor: 'red',
        tooltip: {
        },
        navigatorOptions: {
          // color: HighCharts.getOptions().colors[0]
        },
        data: [],
        dataGrouping: {
          units: dataGroup
        },
        id: 'sz'
      },{
        name: 'Trading Volumn',
        type: 'column',
        data: [],
        yAxis: 1,
        dataGrouping: {
          units: dataGroup
        }
      }]
		})
    // 股票价格数据引用
    const stockPriceData = toRef(stockPriceOptions.series[0], 'data')
    // 股票蜡烛图数据引用
    const stockCandleData = toRef(KLineOptions.series[0], 'data')
    // 股票交易数据引用
    const stockTradingVolumn = toRef(KLineOptions.series[1], 'data')
    // 获取最后预测点日期，方便对其进行高亮处理
    const lastPointDate = toRef(stockPriceOptions.series[0].zones[0], 'value')
    // 股票预测数据引用
    const stockPredictorData = toRef(stockPredictorOptions.series[0], 'data')
    // 股票打分数据引用
    const stockScoreData = toRef(stockScoreOptions.series[0], 'data')
    const emotionScore = toRef(stockEmotionOptions.series[0], 'data')
    // 股票打分请求
    const getScoreData = () => {
      axios
        .get(stock_score_api, {
          params: {
            stock_id: GlobalData.searchedStockInfo.stock_id
          }
        })
        .then(res => {
          const data = res.data
              , isValid = data.code === 1006
          if(!isValid) {
            // 错误操作
            return
          }
          stockScoreData.value = data.data
        })
    }
    // 情感分析，乐观程度
    const getEmotionAnalysis = async () => {
      const res = await axios.get(emotion_analysis_api, {
        params: {
          stock_id: GlobalData.searchedStockInfo.stock_id,
        }
      })
      emotionScore.value = [res.data.data]
    }

    onMounted(() => {
      // 打分
      getScoreData()
      // 乐观程度
      getEmotionAnalysis()
      // 股票数据
      axios
        .post(stock_api, GlobalData.searchedStockInfo)
        .then(res => {
          const data = res.data
          const isSuccess = data.code === 1004
          if(!isSuccess) {
            // TODO: 未成功的操作
            return
          }
          const predictData = data.data
          // 预测得到的下一天的价格
          const predict_price = predictData.predict_price
          // 预测得到的买入，卖出，继续持有概率
          const [buyRate, sellRate, holdRate] = predictData.rates;
          stockPredictorData.value.push({
            name: 'Buy',
            y: buyRate,
          }, {
            name: 'Sell',
            y: sellRate,
          }, {
            name: 'Hold',
            y: holdRate
          })
          const stockData = predictData.stock_price_data
              , dataLength = stockData.length
              , lastDate = stockData[dataLength-1][0]+86400000
          // 压入预测值
          stockPriceData.value = stockData
          stockPriceData.value.push(
            [lastDate, predict_price, 0, 0, 0, 0, 0]
          )
          // 最后一个点日期
          lastPointDate.value = lastDate - 86400000

          for (let i=0; i < dataLength; i++) {
            stockCandleData.value.push([
                stockData[i][0], // the date
                stockData[i][1], // open
                stockData[i][2], // high
                stockData[i][3], // low
                stockData[i][4] // close
            ]);
            stockTradingVolumn.value.push([
                stockData[i][0], // the date
                stockData[i][5] // the volume
            ]);
          }
        })
    })
    const router = useRouter()
    const back = () => {
      router.back()
    }

    // 股票加自选
    const focusStock = () => {
      const stock_id = GlobalData.searchedStockInfo.stock_id
      axios
        .patch(stock_api, {
          stock_id,
        })
        .then(res => {
          const data = res.data
          const isValid = data.code === 1003
          if(!isValid) {
            return
          }
          GlobalData.stockFocus.value.push({
            stock_id,
            stock_publisher: getPublisherByStockId(stock_id)
          })
        })
    }

    const isFocus = computed(() => {
      const length = GlobalData.stockFocus.value.length
      for(let i=0;i<length;++i) {
        const match = GlobalData.stockFocus.value[i].stock_id
                    === GlobalData.searchedStockInfo.stock_id
        if(match) {
          return true
        }
      }
      return false
    })

    return {
      back,
      isFocus,
      KLineOptions,
      stockPriceOptions,
      stockPriceData,
      stockPredictorOptions,
      stockScoreOptions,
      stockEmotionOptions,
      focusStock,
      searchedStockInfo: GlobalData.searchedStockInfo,

    }
  }

})
</script>

<style lang="scss">
  .stock-id {

    font-size: 24px;
    font-weight: bold;
    .item-tab__content {
      width: 96%;
      display: flex;
      align-items: center;
      justify-content: space-between;
    }
    i {
      margin-right: 20px;
      cursor: pointer;
    }
    span {
      text-transform: uppercase;
    }
  }
  .stock-predictor {
    display: flex;
    flex-wrap: wrap;
    .item-tab {
      max-width: 60% !important;
    }
  }
  .stock-score {
    display: flex;
    .item-tab {
      max-width: 60% !important;
    }
  }
</style>
